import styles from './styles/sideMenu.module.css'


function Block({children}){
    return (
        <div className="flex justify-between items-center w-full h-16 border-b border-gray-300 bg-gray-100"> {/* title area*/}
        {children}
        </div>
    )
}


function SideMenu ({menuClick, changeMenuClicked, currentPage, changeCurrentPage, categories, max_shown, links}) {
    return (
        <nav className={`${menuClick ? "transform translate-x-0 transition-all ease-out duration-500": "transform -translate-x-full transition-all ease-out duration-500 "} fixed flex-row w-11/12 z-30 h-full bg-white`}>
            <div className="flex justify-between items-center w-full h-16 border-b border-gray-300 bg-gray-100"> {/* title area*/}
                {/* {console.log("menu : ", menuClick ? "clicked ": "not clicked ")} */}
                <div className="flex justify-center items-center h-full p-5 bg-gray-400 border">
                    <h1>{currentPage.page}</h1>
                </div>
                <div className="flex justify-center items-center p-5 h-full ">
                    <h1>close</h1>
                </div>
            </div>
            <Block>
                <h1>hi there</h1>
            </Block>
            <Block>
                <h1>hi focker</h1>
            </Block>
        </nav>
    )
}

export default SideMenu